/* unplugin-vue-components disabled */import store from '../store/index';
import api from '../api/index';
import DetailDescription from './DetailDescription.vue';
import { ref } from 'vue';
import UploadImage from './UploadImage.vue';
export default {
  name: "AddMedicine",

  data() {
    return {
      isPhone: false,
      formWidth: "13%",
      locations: [],
      form: {
        id: -1,
        brandname: "",
        image: "",
        quantity: "",
        validity: "",
        dosage: "",
        dosagetype: "",
        quantitytype: "",
        manufacturername: "",
        genericname: "",
        producttype: "",
        route: "",
        description: "",
        usage: "",
        warnings: "",
        contraindications: "",
        adversereaction: "",
        overdosage: "",
        username: "",
        locationid: ""
      },
      mediNote: "",
      FDAResult: null,
      dosageFreq: ""
    };
  },

  props: {},
  methods: {
    onSubmit() {
      var validFlag = this.isFormValid(this.form);

      if (validFlag.type === "error") {
        ElMessage.error(validFlag.msg);
        return null;
      } else if (validFlag.type === "warning") {
        ElMessage({
          message: validFlag.msg,
          type: 'warning'
        });
      }

      this.form.dosagetype = this.form.dosagetype + this.dosageFreq;
      this.form.validity = this.form.validity.toLocaleDateString('en-GB');
      console.log("Medicine Information");
      console.log(this.form);
      var that = this;
      api.addMedicine(this.form).then(res => {
        console.log(res);

        if (res.data.status === 200) {
          if (that.mediNote !== "") {
            that.addNote(res.data.data);
          }

          ElMessage({
            message: 'SUCCESS',
            type: 'success'
          });
          that.triggerUpdate();
        }
      });
    },

    addNote(id) {
      console.log("id : " + id);
      console.log(this.mediNote);
      api.addMediNote(id, this.mediNote);
    },

    triggerUpdate() {
      this.$emit("medicine-updated");
      this.backtoDashboard();
    },

    backtoDashboard() {
      console.log("back to dashboard triggered");
      var mode = {
        view: 'default'
      };
      this.$emit("switch-event", mode);
    },

    isFormValid(form) {
      var results = {
        state: true,
        msg: "Valid form",
        type: "success"
      };

      if (form.brandname.length <= 0) {
        results.state = false, results.msg = "Brand name missing";
        results.type = "error";
        return results;
      }

      if (form.validity === "") {
        results.state = false, results.msg = "Expiration date missing";
        results.type = "error";
        return results;
      }

      if (form.quantity === "") {
        results.state = false, results.msg = "Quantity missing";
        results.type = "error";
        return results;
      }

      if (form.quantitytype === "") {
        results.state = false, results.msg = "Quantity Unit missing";
        results.type = "error";
        return results;
      }

      if (form.dosage === "") {
        results.state = false, results.msg = "Dosage missing";
        results.type = "error";
        return results;
      }

      if (form.dosagetype === "") {
        results.state = false, results.msg = "Dosage Unit missing";
        results.type = "error";
        return results;
      }

      if (this.dosageFreq === "") {
        results.state = false, results.msg = "Dosage frequency missing";
        results.type = "error";
        return results;
      }

      if (form.locations === "") {
        results.state = false, results.msg = "Location missing";
        results.type = "warning";
        return results;
      }

      if (form.image === "") {
        results.state = false, results.msg = "Image missing";
        results.type = "warning";
        return results;
      }

      return results;
    },

    getFDAInfo(brandname) {
      console.log("User entered " + brandname);
      var name = brandname.toUpperCase();
      var that = this;

      if (name !== "") {
        api.getFDAData(name).then(res => {
          console.log(res.data);

          try {
            that.FDAResult = res.data.results[0];
          } catch (e) {
            console.error(e);
          } // that.FDAResult = res.data.results[0]


          try {
            that.form.manufacturername = that.FDAResult.openfda.manufacturer_name[0];
          } catch (e) {
            console.error(e);
          } //that.form.manufacturername = that.FDAResult.openfda.manufacturer_name[0]


          try {
            that.form.genericname = that.FDAResult.openfda.generic_name[0];
          } catch (e) {
            console.error(e);
          } //that.form.genericname = that.FDAResult.openfda.generic_name[0]


          try {
            that.form.producttype = that.FDAResult.openfda.product_type[0];
          } catch (e) {
            console.error(e);
          } //that.form.producttype = that.FDAResult.openfda.product_type[0]


          try {
            that.form.route = that.FDAResult.openfda.route[0];
          } catch (e) {
            console.error(e);
          } //that.form.route = that.FDAResult.openfda.route[0]


          try {
            that.form.description = that.FDAResult.description[0];
          } catch (e) {
            console.error(e);
          } //that.form.description = that.FDAResult.description[0]


          try {
            that.form.usage = that.FDAResult.indications_and_usage[0];
          } catch (e) {
            console.error(e);
          } //that.form.usage = that.FDAResult.indications_and_usage[0]


          try {
            that.form.warnings = that.FDAResult.warnings_and_cautions[0];
          } catch (e) {
            console.error(e);
          } //that.form.warnings = that.FDAResult.warnings_and_cautions[0]


          try {
            that.form.contraindications = that.FDAResult.contraindications[0];
          } catch (e) {
            console.error(e);
          } //that.form.contraindications = that.FDAResult.contraindications[0]


          try {
            that.form.adversereaction = that.FDAResult.adverse_reactions[0];
          } catch (e) {
            console.error(e);
          } //that.form.adversereaction = that.FDAResult.adverse_reactions[0]


          try {
            that.form.overdosage = that.FDAResult.overdosage[0];
          } catch (e) {
            console.error(e);
          } //that.form.overdosage = that.FDAResult.overdosage[0]


          ElMessage({
            message: "FDA data is ready",
            type: 'success'
          });
        }, () => {
          console.log("Medicine Name Not Found");
          ElMessage({
            message: "No FDA data found",
            type: 'warning'
          });
        });
      } else {
        console.log("No input");
      }
    },

    handleImageUpload(url) {
      console.log("Image URL is " + url);
      this.form.image = url;
    },

    handleImageRemoved(file) {
      console.log("Image deleted");
      this.form.image = "";
    }

  },

  created() {
    this.isPhone = !(window.innerWidth > 992);
    this.locations = store.getters.getLocations;

    if (this.isPhone) {
      this.formWidth = "22%";
    }

    this.form.username = store.getters.getUserName;
  },

  components: {
    DetailDescription,
    UploadImage
  }
};