export default {
  name: 'MedicineListPagination',
  props: {
    total: {
      type: Number,
      default: 20
    },
    page: {
      type: Number,
      default: 1
    },
    pagesize: {
      type: Number,
      default: 10
    },
    totalPages: {
      type: Number,
      default: 1
    }
  },

  created() {
    this.getCurrentPage();
  },

  methods: {
    changePage(val) {
      this.handlePage('push', val, this.pagesize);
      this.$emit('change', val, this.pagesize);
    },

    getCurrentPage() {
      var {
        page,
        pagesize
      } = this.$route.query;

      if (!page || !pagesize) {
        this.handlePage('replace', page || 1, +pagesize || this.pagesize);
        return true;
      }

      return false;
    },

    handlePage(type, page, pagesize) {
      this.$router[type]({
        path: this.$route.path,
        query: { ...this.$route.query,
          page,
          pagesize
        }
      });
    }

  }
};