import DetailDescription from "./DetailDescription.vue";
import api from "../api/index";
import { nextTick } from '@vue/runtime-core';
export default {
  data() {
    return {
      mediInfo: null,
      isDataReady: false,
      imageBasePath: "https://medione.herokuapp.com/",
      userNote: "",
      dialogFormVisible1: false,
      dialogFormVisible2: false,
      amount: "1"
    };
  },

  props: {
    mediId: {
      type: Number,
      default: -1
    }
  },
  components: {
    DetailDescription
  },
  methods: {
    noteButton() {
      var that = this;
      this.addNote();
      console.log(that.userNote);
    },

    addNote() {
      var that = this;
      api.addMediNote(this.mediId, that.userNote).then(res => {
        // console.log(that.userNote)
        console.log(res.data);
      });
    },

    openDialog1() {
      var that = this;
      this.dialogFormVisible1 = true;
      console.log(that.tableForm1.amount);
    },

    openDialog2() {
      this.dialogFormVisible2 = true;
      console.log(this.dialogFormVisible2);
    },

    takeConfirmButon() {
      var that = this;
      api.takeMedicine(that.mediId, that.amount).then(res => {
        console.log(res.data);
        that.dialogFormVisible1 = false;

        if (res.data.status == 200) {
          console.log(res.data.status);
          alert('Update successful, please refresh!');
          console.log(res.data.status);
        } else if (res.data.status == 404) {
          alert('Update failed, the quantity of medicines entered exceeds the remaining quantity, please verify and try again.');
        }
      });
    },

    deleteConfirmButon() {
      var that = this;
      api.deleteMedicine(that.mediId).then(res => {
        console.log(res.data);
        that.dialogFormVisible1 = false;

        if (res.data.status == 200) {
          this.triggerUpdate();
        }
      });
    },

    triggerUpdate() {
      console.log("Update medidata");
      this.$emit("medicine-updated");
      this.backToPrev();
    },

    backToPrev() {
      var mode = {
        view: 'default'
      };
      this.$emit("switch-event", mode);
    }

  },

  created() {
    var that = this;
    console.log(this.mediId);
    api.getMediInfo(this.mediId).then(res => {
      that.mediInfo = res.data.data;
      console.log(that.mediInfo);
      that.isDataReady = true;
    });
    api.getMediNote(this.mediId).then(res => {
      that.userNote = res.data.data.note;
      console.log(res.data);
    });
  },

  mounted() {// var that = this
    // console.log(this.mediId)
    // api.getMediInfo(this.mediId).then(res=>{
    // 	that.mediInfo = res.data.data
    // })
  }

};